import { useState, useRef, useEffect } from "react";
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import styles from '@/views/partials/card.module.css';
import clsx from 'clsx';
import 'bootstrap';



function DisplayCard(props: any, onEdit: any) {
    const [dCard] = useState({
        task: props.task,
        id: props.id,
        description: props.description,
        due_date: props.due_date,
        priority: props.priority
    });
    function editForm() {
        props.onEdit(dCard);
    }


    return (
        <div className={styles.cardParent} style={{height: '300px'}}>
            <div className={styles.card}>
               <div><h3>{dCard.task}</h3></div>
               <div className="d-flex"><p>{dCard.description}</p></div>
                <div className={styles.bottomSection}>
                    <p>Due: {dCard.due_date && new Date(dCard.due_date).toDateString() ||  'N/A'}</p>
                    <p>Priority: {dCard.priority}</p>
                </div>
                <div className={styles.icons}>
                    <button className={clsx(styles.edit, styles['my-button'])} onClick={editForm}>
                        <EditIcon style={{ color: 'white' }} />
                    </button>
                    <button className={clsx(styles.delete, styles['my-button'])}>
                        <DeleteIcon style={{ color: 'white' }} />
                    </button>
                </div>       
            </div>
        </div>
    );

}

export default DisplayCard;